import React from 'react';
import { cssDecorator } from '@front-toolkit/utils';

import styles from './Button.module.scss';

const cd = cssDecorator(styles);

export interface ButtonProps {
  /** Флаг кликабельности кнопки */
  disabled?: boolean;

  /** ID для тестирования */
  dataTestId?: string;

  /** Колбэк нажатия на кнопку */
  onClick(): void;
}

/** Кнопка элемента управления редактора */
const Button: React.FC<ButtonProps> = ({
  children,
  disabled,
  dataTestId,
  onClick
}) => (
  <button
    className={ cd('button', { disabled }) }
    onClick={ onClick }
    type="button"
    disabled={ disabled }
    data-test-id={ dataTestId }
  >
    { children }
  </button>
);

export default Button;
